"use client"

import { useState } from "react"
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query"
import { Calendar, Plus, Trash2, AlertCircle, X } from "lucide-react"
import { formatDate } from "@/lib/utils"
import toast from "react-hot-toast"

function HolidaysSkeleton() {
  return (
    <div className="p-6 lg:p-8">
      <div className="max-w-7xl mx-auto space-y-6">
        <div className="h-8 w-48 bg-gray-200 rounded-lg animate-pulse" />
        <div className="space-y-4">
          {[1, 2, 3].map(i => (
            <div key={i} className="h-24 bg-white rounded-2xl animate-pulse" />
          ))}
        </div>
      </div>
    </div>
  )
}

export default function HolidaysPage() {
  const queryClient = useQueryClient()
  const [dialogOpen, setDialogOpen] = useState(false)
  const [date, setDate] = useState("")
  const [reason, setReason] = useState("")

  const { data, isLoading } = useQuery({
    queryKey: ["holidays"],
    queryFn: async () => {
      const res = await fetch("/api/admin/holidays")
      if (!res.ok) throw new Error("Failed to fetch")
      return res.json()
    },
  })

  const createMutation = useMutation({
    mutationFn: async () => {
      const res = await fetch("/api/admin/holidays", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ date, reason }),
      })
      if (!res.ok) {
        const data = await res.json()
        throw new Error(data.error)
      }
      return res.json()
    },
    onSuccess: (data) => {
      toast.success(`Holiday created. ${data.data.affectedCustomers} customers affected.`)
      queryClient.invalidateQueries({ queryKey: ["holidays"] })
      setDialogOpen(false)
      setDate("")
      setReason("")
    },
    onError: (err: any) => {
      toast.error(err.message)
    },
  })

  const deleteMutation = useMutation({
    mutationFn: async (holidayDate: string) => {
      const res = await fetch(`/api/admin/holidays?date=${holidayDate}`, {
        method: "DELETE",
      })
      if (!res.ok) {
        const data = await res.json()
        throw new Error(data.error)
      }
      return res.json()
    },
    onSuccess: () => {
      toast.success("Holiday removed")
      queryClient.invalidateQueries({ queryKey: ["holidays"] })
    },
    onError: (err: any) => {
      toast.error(err.message)
    },
  })

  if (isLoading) return <HolidaysSkeleton />

  const holidays = data?.data?.holidays || []

  // Get tomorrow's date for min date
  const tomorrow = new Date()
  tomorrow.setDate(tomorrow.getDate() + 1)
  const minDate = tomorrow.toISOString().split('T')[0]

  return (
    <div className="p-6 lg:p-8">
      <div className="max-w-7xl mx-auto space-y-6">
        
        {/* Page Header */}
        <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Holidays</h1>
            <p className="text-gray-500 mt-1">Manage delivery holidays for all customers</p>
          </div>
          <button
            onClick={() => setDialogOpen(true)}
            className="inline-flex items-center gap-2 px-5 py-2.5 bg-slate-900 text-white font-medium rounded-xl hover:bg-slate-800 transition-colors"
          >
            <Plus className="w-5 h-5" />
            Add Holiday
          </button>
        </div>

        {/* Add Holiday Dialog */}
        {dialogOpen && (
          <div className="fixed inset-0 z-50 flex items-center justify-center">
            <div className="absolute inset-0 bg-black/50" onClick={() => setDialogOpen(false)} />
            <div className="relative bg-white rounded-2xl shadow-xl w-full max-w-md mx-4 p-6">
              <div className="flex items-center justify-between mb-6">
                <div>
                  <h2 className="text-xl font-bold text-gray-900">Declare Holiday</h2>
                  <p className="text-sm text-gray-500 mt-1">Pause delivery for all customers</p>
                </div>
                <button
                  onClick={() => setDialogOpen(false)}
                  className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
                >
                  <X className="w-5 h-5 text-gray-500" />
                </button>
              </div>
              
              <div className="space-y-4">
                <div className="p-4 bg-amber-50 border border-amber-200 rounded-xl">
                  <div className="flex gap-3 text-amber-800 text-sm">
                    <AlertCircle className="w-5 h-5 flex-shrink-0 mt-0.5" />
                    <p>Customers won't be charged for this day, and no deliveries will be scheduled.</p>
                  </div>
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1.5">Date</label>
                  <input
                    type="date"
                    value={date}
                    onChange={(e) => setDate(e.target.value)}
                    min={minDate}
                    className="w-full h-11 px-4 border border-gray-200 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1.5">Reason</label>
                  <input
                    type="text"
                    value={reason}
                    onChange={(e) => setReason(e.target.value)}
                    placeholder="e.g., Festival, Maintenance"
                    className="w-full h-11 px-4 border border-gray-200 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                  />
                </div>
              </div>

              <div className="flex gap-3 mt-6">
                <button
                  onClick={() => setDialogOpen(false)}
                  className="flex-1 h-11 px-4 border border-gray-200 rounded-xl font-medium text-gray-700 hover:bg-gray-50 transition-colors"
                >
                  Cancel
                </button>
                <button
                  onClick={() => createMutation.mutate()}
                  disabled={!date || !reason || createMutation.isPending}
                  className="flex-1 h-11 px-4 bg-slate-900 text-white rounded-xl font-medium hover:bg-slate-800 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                >
                  {createMutation.isPending ? "Creating..." : "Declare Holiday"}
                </button>
              </div>
            </div>
          </div>
        )}

        {/* Holidays List */}
        {holidays.length === 0 ? (
          <div className="bg-white rounded-2xl border border-gray-200 shadow-sm p-12">
            <div className="text-center">
              <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <Calendar className="w-8 h-8 text-gray-400" />
              </div>
              <h3 className="text-lg font-semibold text-gray-900 mb-1">No upcoming holidays</h3>
              <p className="text-gray-500 mb-6">Declare holidays to pause deliveries for all customers on specific dates</p>
              <button
                onClick={() => setDialogOpen(true)}
                className="inline-flex items-center gap-2 px-5 py-2.5 bg-slate-900 text-white font-medium rounded-xl hover:bg-slate-800 transition-colors"
              >
                <Plus className="w-4 h-4" />
                Add Holiday
              </button>
            </div>
          </div>
        ) : (
          <div className="space-y-4">
            {holidays.map((holiday: any) => {
              const holidayDate = new Date(holiday.date)
              const isPast = holidayDate < new Date()

              return (
                <div 
                  key={holiday.id} 
                  className={`bg-white rounded-2xl border border-gray-200 shadow-sm p-5 ${isPast ? "opacity-60" : ""}`}
                >
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-5">
                      <div className="w-16 h-16 bg-red-50 border border-red-200 rounded-2xl flex flex-col items-center justify-center flex-shrink-0">
                        <span className="text-xs text-red-600 font-semibold uppercase">
                          {holidayDate.toLocaleString('default', { month: 'short' })}
                        </span>
                        <span className="text-2xl font-bold text-red-700">
                          {holidayDate.getDate()}
                        </span>
                      </div>
                      <div>
                        <p className="text-lg font-semibold text-gray-900">{holiday.reason}</p>
                        <p className="text-sm text-gray-500">
                          {formatDate(holiday.date)} Â· {holidayDate.toLocaleString('default', { weekday: 'long' })}
                        </p>
                      </div>
                    </div>
                    {!isPast && (
                      <button
                        onClick={() => {
                          if (confirm("Remove this holiday? Deliveries will be scheduled again.")) {
                            deleteMutation.mutate(holidayDate.toISOString().split('T')[0])
                          }
                        }}
                        className="p-2.5 text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                      >
                        <Trash2 className="w-5 h-5" />
                      </button>
                    )}
                  </div>
                </div>
              )
            })}
          </div>
        )}

      </div>
    </div>
  )
}
